import os from 'os';

async function getData(){
  // 公网ip地址
  // @ts-ignore
  let publicIp = '';
  let city = '';
  let country = '';
  let province = '';
  let address = '';
  // 上报内容
  const env = process.env;
  // 标识app
  const appType = 'secretary';
  // 时间
  const time = new Date().toUTCString();
  // Dashboard 版本号
  const dashBoardVersion = env.dashboard;
  // Creator 版本号
  const creatorVersion = await Editor.App.version;
  // 语言类型
  const locate = await Editor.I18n.getLanguage();
  // 当前Cocos账号信息，昵称，邮箱，cocos_uid
  const userData = await Editor.User.getData();
  const nickName = userData.nickname;
  const email = userData.email;
  const cocos_uid = userData.cocos_uid;
  // IPs
  const ips = await Editor.Network.queryIPList();
  // 返回操作系统名
  const osType = os.type();
  // 返回编译时的操作系统名字
  const osPlatform = os.platform();
  // 返回操作系统的主机名。
  const osHostname = os.hostname();
  // 返回操作系统的默认临时文件夹。
  const osTmpdir = os.tmpdir();
  // 返回操作系统 CPU 架构，可能的值有 "x64"、"arm" 和 "ia32"。
  const osArch = os.arch();
  // 当前项目名称、当前项目包名
  // @ts-ignore
  const currentPjName =  Editor.Project.name;
  return  {
    appType,
    time,
    dashBoardVersion,
    creatorVersion,
    city,
    country,
    province,
    address,
    locate,
    userData,
    nickName,
    email,
    cocos_uid,
    ips,
    osType,
    osPlatform,
    osHostname,
    osTmpdir,
    publicIp,
    osArch,
    currentPjName,
  }
}
